.sharer-buttons-toolbar {
    @each $name, $color in $sharer-buttons {
        button.sharer-rounded--#{$name} {
            display: inline-block;
            padding: 4px 10px ;
            border: 1px solid transparent;
            margin: 0 auto 8px;
            border-radius: 5px;
            font-size: 15px;
            color: #fff;
            opacity: .89;
            transition: opacity 0.15s linear;

            .sharer-icon {
                font-size: 16px;
                line-height: 1.5;
                vertical-align: middle;

                svg path {
                    fill: #fff;
                }
            }

            .sharer-label {
                vertical-align: middle;
            }

            &:hover {
                opacity: 1;
            }
        }
    }
}